<template>
  <div>
    <div style="height: 100%;">
      <div style="display: flex; align-items: center;">
        <div class="container left">
          <actual-consumption ref="actualConsume" />
          <tech-consumption ref="techConsume" />
          <theory-consumption ref="theoryConsume" />
        </div>
        <div id="chartContainer" style="width: 2000px; height: 900px;"></div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import ActualConsumption from "./actualConsumption.vue";
import TechConsumption from "./techConsumption.vue";
import TheoryConsumption from "./theoryConsumption.vue";
import * as echarts from 'echarts';


export default {
  name: 'EnergyView',
  components: {
    ActualConsumption,
    TechConsumption,
    TheoryConsumption,
  },
  data() {
    return {
      consume1: 0,
      consume2: 0,
      consume3: 0,
      chart: null,
    };
  },
  created() {
    // ...
  },
  mounted() {
    this.$nextTick(() => {
      // 监听子组件数据的变化
      this.$watch(
        () => this.$refs.actualConsume.consume1,
        (newVal) => {
          this.consume1 = newVal;
          this.updateChart();
        }
      );
      this.$watch(
        () => this.$refs.techConsume.consume2,
        (newVal) => {
          this.consume2 = newVal;
          this.updateChart();
        }
      );
      this.$watch(
        () => this.$refs.theoryConsume.consume3,
        (newVal) => {
          this.consume3 = newVal;
          this.updateChart();
        }
      );
    });

    const chartContainer = document.getElementById('chartContainer');
    this.chart = echarts.init(chartContainer);


    this.updateChart();
  },
  methods: {
    updateChart() {
      const options = {
        textStyle: {
          color: 'white'
        },
        legend: {
          orient: 'horizontal',
          right: 'center',
          top: 'top',
          textStyle: {
            color: 'white'
          }
        },
        title: {
          text: '工序能耗详细信息',
          textStyle: {
            color: 'yellow'
          }
        },
        tooltip: {},
        xAxis: {
          data: ['工序实际能耗', '工序技术能耗', '工序理论能耗'],
          axisLabel: {
            color: 'yellow'
          }
        },
        yAxis: {
          min: 0,
          max: 30000,
          axisLabel: {
            color: 'white'
          }
        },
        series: [{
          data: [this.consume1, this.consume2, this.consume3],
          type: 'bar',
          stack: 'x',
          name: 'Consumption'
        }]
      };
      console.log(this.consume1, this.consume2, this.consume3);
      this.chart.setOption(options);
    }
  }
};
</script>