<template>
  <div style="width: 600px; padding: 5px 0;">
    <p class="title">实际工序能耗: {{ consume1 }} kgce/t-s<button @click="saveAndSubmit()">计算</button></p>
    <div class="container">
      <div class="left">
        <ul>
          <li v-for="item in consumptionItems" :key="item.index">
            <span class="ll" style="width: 140px; display: inline-block; line-height: 30px;">{{ item.label }} {{ item.unit
            }}</span>
            <!-- <span style="width: 120px; display: inline-block; line-height: 30px;"></span> -->
            <input type="text" v-model="item.text" class="narrow-input" />
          </li>
        </ul>
      </div>
      <div class="right">
        <ul>
          <li v-for="item in coefficientItems" :key="item.index">
            <span class="rr" style="width: 140px; display: inline-block; line-height: 30px;">{{ item.label }}折算系数</span>
            <input type="text" v-model="item.text" class="narrow-input" />
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
  
<script>
import { EventBus } from '@/EventBus.js'
import { ajaxPost } from '../../util/ajax';
import axios from 'axios';

export default {
  name: 'actualConsumption',
  data() {
    return {
      consume1: 0,
      consumptionItems: [
        { label: '焦粉消耗', unit: 'kg', text: '', backendName: "cokeConsumption" },
        { label: '无烟煤消耗', unit: 'Nm3', text: '', backendName: "coalConsumption" },
        { label: 'BFG', unit: 'Nm3', text: '', backendName: "bfgconsumption" },
        { label: 'COG', unit: 'Nm3', text: '', backendName: "cogconsumption" },
        { label: '电耗', unit: 'Kwh', text: '', backendName: "electricityConsumption" },
        { label: '水耗', unit: 'kg', text: '', backendName: "waterConsumption" },
        { label: 'N2介质', unit: 'kg', text: '', backendName: "n2Consumption" },
        { label: '回收蒸汽', unit: 'kg', text: '', backendName: "steamConsumption" },
      ],
      coefficientItems: [
        { label: '焦粉', text: '', backendName: "coke" },
        { label: '无烟煤', text: '', backendName: "coal" },
        { label: 'BFG', text: '', backendName: 'BFG' },
        { label: 'COG', text: '', backendName: "COG" },
        { label: '电耗', text: '', backendName: "electricity" },
        { label: '水耗', text: '', backendName: "water" },
        { label: 'N2', text: '', backendName: "N2" },
        { label: '回收蒸汽', text: '', backendName: "steam" },
      ]
    }
  },
  methods: {
    saveAndSubmit() {

      this.saveToMaterialData();
      this.submitData();
      this.callSubmitToStore();
    },
    saveToMaterialData() {
      function searchInItems(items, backendName) {
        for (let i = 0; i < items.length; i++) {
          const element = items[i];
          if (element.backendName == backendName) {
            // console.log(element.backendName, backendName, element.text);
            return Number(element.text);

          }
        }

      }
      let md = this.$store.state.materialData;
      console.log('in energy-efficiency actual, materialData', md);

      md.ratioConvert.coke = searchInItems(this.coefficientItems, "coke");//以下需要改成这样
      md.ratioConvert.coal = searchInItems(this.coefficientItems, "coal");
      md.ratioConvert.BFG = searchInItems(this.coefficientItems, "BFG");
      md.ratioConvert.COG = searchInItems(this.coefficientItems, "COG");
      md.ratioConvert.electricity = searchInItems(this.coefficientItems, "electricity");
      md.ratioConvert.water = searchInItems(this.coefficientItems, "water");
      md.ratioConvert.N2 = searchInItems(this.coefficientItems, "N2");
      md.ratioConvert.steam = searchInItems(this.coefficientItems, "steam");
      this.$store.commit("setMaterialData", md)
      // console.log(this.$store.state.materialData);
      // this.sendDataToBackend()
    },
    submitData() {
      // const formData = {
      //     consumptionItems: this.consumptionItems.map(item => ({ label: item.label, value: item.value }))
      // };

      ajaxPost('/Actual/GetActualVo', this.$store.state.materialData)
        .then(response => {
          // 从后端获取值，并赋给 consumptionItems 数组中的每个对象的 text 属性
          const backendValues = response.data;
          this.consumptionItems.forEach(item => {
            item.text = backendValues[item.backendName];
          });
          console.log(this.consumptionItems);
          this.searchInBackendComputedReturnData(this.consumptionItems, backendValues.data);
          function searchInItems(items, backendName) {
            for (let i = 0; i < items.length; i++) {
              const element = items[i];
              if (element.backendName == backendName) {
                // console.log(element.backendName, backendName, element.text);
                return Number(element.text);

              }
            }

          }
          // console.log(searchInItems(this.coefficientItems, "coke")*response.data.data.cokeConsumption);
          // console.log(typeof(response.data.data.cokeConsumption));
          // console.log(response.data);
          this.consume1 = searchInItems(this.coefficientItems, "coke") * response.data.data.cokeConsumption + searchInItems(this.coefficientItems, "coal") * response.data.data.coalConsumption + searchInItems(this.coefficientItems, "BFG") * response.data.data.bfgconsumption + searchInItems(this.coefficientItems, "COG") * response.data.data.cogconsumption + searchInItems(this.coefficientItems, "electricity") * response.data.data.electricityConsumption + searchInItems(this.coefficientItems, "water") * response.data.data.waterConsumption + searchInItems(this.coefficientItems, "N2") * response.data.data.n2Consumption + searchInItems(this.coefficientItems, "steam") * response.data.data.steamConsumption;
          this.consume1 = this.consume1.toFixed(2);
        })
        .catch(error => {
          console.error(error); // 处理错误响应
        });
    },
    /**
     * 将后端获取的结果（backendDataArr）赋值到第一个参数（elementArr）里
     * 如果elementArr的数据项内，存在backendName属性，则按照backendName匹配
     * 如果不存在，则按照label匹配
     * @param {*} elementArr 画面变量
     * @param {*} backendDataArr 后端获取到的数据
     */
    searchInBackendReturnData(elementArr, backendDataArr) {
      for (let j = 0; j < elementArr.length; j++) {
        const element = elementArr[j];
        for (let index = 0; index < backendDataArr.length; index++) {
          let flag = 0;
          if (element.backendName != undefined) {
            // console.log(element);
            flag = backendDataArr[index].name == element.backendName
          }
          else {
            flag = backendDataArr[index].annotation.match(element.label)
          }
          if (flag) {
            element.text = backendDataArr[index].ratio
            break
          }
        }
      }
    },

    searchInBackendComputedReturnData(elementArr, backendData) {
      for (let j = 0; j < elementArr.length; j++) {
        const element = elementArr[j];
        for (const key in backendData) {
          if (Object.hasOwnProperty.call(backendData, key)) {
            // const element = backendData[key];
            // console.log(key, element);
            let flag = 0;
            flag = key == element.backendName;
            if (flag) {
              element.text = backendData[key];
              element.text = element.text.toFixed(2);//
              break;
            }
          }
        }


      }
    },
    callSubmitToStore() {
      EventBus.$emit('saveAndSubmit') // 触发自定义事件
    }
  },

  created() {
    axios.post('/Actual/getConvertRatio')
      .then(response => {
        // 处理请求成功的响应
        this.flag = response.data.flag; // 根据API返回的数据结构更新data1的值

        this.searchInBackendReturnData(this.coefficientItems, response.data)
        //    this.searchInBackendReturnData(this.consumptionItems, response.data)

      })
      .catch(error => {
        // 处理请求失败的错误
        console.error(error);
      });
  }
}
</script>
  
  
