<template>
  <div style="width: 600px; padding: 5px 0;">
    <p class="title">理论极致能耗 {{ consume3 }} kgce/t-s
      <!-- <button @click="saveAndSubmit()">计算</button> -->
    </p>
    <div class="container">
      <div class="left">
        <ul>
          <li v-for="item in consumptionItems" :key="item.index">
            <span class="ll" style="width: 140px; display: inline-block; line-height: 30px;">{{ item.label }} {{ item.unit
            }}</span>
            <!-- <span style="width: 120px; display: inline-block; line-height: 30px;"></span> -->
            <input type="text" v-model="item.text" class="narrow-input" />
          </li>
        </ul>
      </div>
      <div class="right">
        <ul>
          <li v-for="item in coefficientItems" :key="item.index">
            <span class="rr" style="width: 140px; display: inline-block; line-height: 30px;">{{ item.label }}折算系数</span>
            <input type="text" v-model="item.text" class="narrow-input" />
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios"
import { EventBus } from '@/EventBus.js'
export default {
  name: 'theoryConsumption',
  data() {
    return ({
      consume3: 0,
      consumptionItems: [
        // { label: '焦粉消耗', unit: 'kg' },
        // { label: '无烟煤消耗', unit: 'Nm3' },
        { label: '煤炭消耗', unit: 'kg', text: '', backendName: "coalConsumption" },
        { label: 'BFG', unit: 'Nm3', backendName: "bfgconsumption" },
        { label: 'COG', unit: 'Nm3', backendName: "cogconsumption" },
        { label: '电耗', unit: 'Kwh', backendName: "electricityConsumption" },
        { label: '水耗', unit: 'kg', backendName: "waterConsumption" },
        { label: 'N2介质', unit: 'kg', backendName: "n2Consumption" },
        { label: '回收蒸汽', unit: 'kg', backendName: "steamConsumption" },

      ],
      coefficientItems: [
        { label: '煤炭消耗', text: '', backendName: "coke" },
        { label: '无烟煤', text: '', backendName: "coal" },
        { label: 'BFG', text: '', backendName: 'BFG' },
        { label: 'COG', text: '', backendName: "COG" },
        { label: '电耗', text: '', backendName: "electricity" },
        { label: '水耗', text: '', backendName: "water" },
        { label: 'N2', text: '', backendName: "N2" },
        { label: '回收蒸汽', text: '', backendName: "steam" },
      ]
    })
  },
  methods: {
    saveAndSubmit() {
      this.saveToTheoryData();
      this.submitData();
    },
    saveToTheoryData() {
      function searchInItems(items, backendName) {
        for (let i = 0; i < items.length; i++) {
          const element = items[i];
          if (element.backendName == backendName) {
            // console.log(element.backendName, backendName, element.text);
            return Number(element.text);

          }
        }

      }
      let md = this.$store.state.theoryData;
      console.log(md);

      md.coke = searchInItems(this.coefficientItems, "coke");//以下需要改成这样
      md.coal = searchInItems(this.coefficientItems, "coal");
      md.BFG = searchInItems(this.coefficientItems, "BFG");
      md.COG = searchInItems(this.coefficientItems, "COG");
      md.electricity = searchInItems(this.coefficientItems, "electricity");
      md.water = searchInItems(this.coefficientItems, "water");
      md.N2 = searchInItems(this.coefficientItems, "N2");
      md.steam = searchInItems(this.coefficientItems, "steam");
      this.$store.commit("setTheoryData", md)
      // console.log(this.$store.state.materialData);
      // this.sendDataToBackend()
    },

    searchInBackendReturnData(elementArr, backendDataArr) {

      for (let j = 0; j < elementArr.length; j++) {
        const element = elementArr[j];
        for (let index = 0; index < backendDataArr.length; index++) {
          let flag = 0;
          if (element.backendName != undefined) {
            // console.log(element);
            flag = backendDataArr[index].name == element.backendName
          }
          else {
            flag = backendDataArr[index].annotation.match(element.label)
          }
          if (flag) {
            element.text = backendDataArr[index].ratio;
            element.text = element.text.toFixed(2);
            break
          }
        }
      }
    },

    submitData() {
      // const formData = {
      //     consumptionItems: this.consumptionItems.map(item => ({ label: item.label, value: item.value }))
      // };
      function searchInItems(items, backendName) {
        for (let i = 0; i < items.length; i++) {
          const element = items[i];
          if (element.backendName == backendName) {
            // console.log(element.backendName, backendName, element.text);
            return Number(element.text);

          }
        }

      }

      axios.post('/Theory/GetResult', this.$store.state.theoryData)
        .then(response => {
          // 从后端获取值，并赋给 consumptionItems 数组中的每个对象的 text 属性
          const backendValues = response.data;
          this.consumptionItems[0].text = 0;
          this.consumptionItems.forEach(item => {
            item.text = backendValues.data[item.backendName];
          });
          // this.searchInBackendComputedReturnData(this.consumptionItems, backendValues.data);
          console.log(this.consumptionItems);
          // this.consumptionItems[0].text = 123;
          //   this.searchInBackendComputedReturnData(this.consumptionItems, backendValues.data);
          //   function searchInItems(items, backendName) {
          //     for(let i = 0; i < items.length; i++) {
          //         const element = items[i];
          //         if (element.backendName == backendName) {
          //             // console.log(element.backendName, backendName, element.text);
          //             return Number(element.text);

          //         }
          //     }

          //     }
          console.log(response.data);
          this.consume3 = searchInItems(this.coefficientItems, "coke") * response.data.data.cokeConsumption + searchInItems(this.coefficientItems, "coal") * response.data.data.coalConsumption + searchInItems(this.coefficientItems, "BFG") * response.data.data.bfgconsumption + searchInItems(this.coefficientItems, "COG") * response.data.data.cogconsumption + searchInItems(this.coefficientItems, "electricity") * response.data.data.electricityConsumption + searchInItems(this.coefficientItems, "water") * response.data.data.waterConsumption + searchInItems(this.coefficientItems, "N2") * response.data.data.n2Consumption + searchInItems(this.coefficientItems, "steam") * response.data.data.steamConsumption;
          this.consume3 = this.consume3.toFixed(2);
          console.log(this.consume3);
        })
        .catch(error => {
          console.error(error); // 处理错误响应
        });
    },

  },
  mounted() {
    EventBus.$on('saveAndSubmit', () => { // 监听自定义事件
      this.saveAndSubmit()
    })
  },
  created() {
    axios.post('/Actual/getConvertRatio')
      .then(response => {
        // 处理请求成功的响应
        // this.flag = response.data.flag; // 根据API返回的数据结构更新data1的值

        this.searchInBackendReturnData(this.coefficientItems, response.data)
        //    this.searchInBackendReturnData(this.consumptionItems, response.data)

      })
      .catch(error => {
        // 处理请求失败的错误
        console.error(error);
      });
  },
  beforeDestroy() {
    // 在离开页面的时候取消监听事件，否则二次进页面会导致事件被多次触发
    EventBus.$off('submitToStore')
  }
}
</script>

